export default "import { Component, ViewChild } from '@angular/core';\nimport { ModalDirective } from 'bao-tran-angular/modal';\n\n@Component({\n  selector: 'demo-modal-auto-shown',\n  templateUrl: './auto-shown.html'\n})\nexport class DemoAutoShownModalComponent {\n  @ViewChild('autoShownModal', { static: false }) autoShownModal: ModalDirective;\n  isModalShown = false;\n\n  showModal(): void {\n    this.isModalShown = true;\n  }\n\n  hideModal(): void {\n    this.autoShownModal.hide();\n  }\n\n  onHidden(): void {\n    this.isModalShown = false;\n  }\n}\n";