export default "import { Component, ViewChild } from '@angular/core';\nimport { ModalDirective } from 'bao-tran-angular/modal';\n\n@Component({\n  selector: 'demo-modal-events',\n  templateUrl: './events.html',\n  styles: [`\n    .card {\n      margin-bottom: 0.75rem;\n      padding: 8px;\n    }\n  `]\n})\nexport class DemoModalEventsComponent {\n  @ViewChild(ModalDirective, { static: false }) modal: ModalDirective;\n  messages: string[];\n\n  showModal() {\n    this.messages = [];\n    this.modal.show();\n  }\n  handler(type: string, $event: ModalDirective) {\n    this.messages.push(\n      `event ${type} is fired${$event.dismissReason\n        ? ', dismissed by ' + $event.dismissReason\n        : ''}`\n    );\n  }\n}\n";