export default "import { Component, TemplateRef } from '@angular/core';\nimport { BsModalService, BsModalRef } from 'bao-tran-angular/modal';\n\n@Component({\n  selector: 'demo-modal-service-custom-css-class',\n  templateUrl: './custom-css-class.html'\n})\nexport class DemoModalServiceCustomCSSClassComponent {\n  modalRef: BsModalRef;\n  constructor(private modalService: BsModalService) {}\n\n  openModalWithClass(template: TemplateRef<any>) {\n    this.modalRef = this.modalService.show(\n      template,\n      Object.assign({}, { class: 'gray modal-lg' })\n    );\n  }\n}\n";