export default "import { Component, TemplateRef } from '@angular/core';\nimport { BsModalService, BsModalRef } from 'bao-tran-angular/modal';\n\n@Component({\n  selector: 'demo-modal-service-nested',\n  templateUrl: './service-nested.html'\n})\nexport class DemoModalServiceNestedComponent {\n  modalRef: BsModalRef | null;\n  modalRef2: BsModalRef;\n  constructor(private modalService: BsModalService) {}\n\n  openModal(template: TemplateRef<any>) {\n    this.modalRef = this.modalService.show(template, { id: 1, class: 'modal-lg' });\n  }\n  openModal2(template: TemplateRef<any>) {\n    this.modalRef2 = this.modalService.show(template, {id: 2, class: 'second' });\n  }\n  closeFirstModal() {\n    if (!this.modalRef) {\n      return;\n    }\n\n    this.modalRef.hide();\n    this.modalRef = null;\n  }\n  closeModal(modalId?: number){\n    this.modalService.hide(modalId);\n  }\n}\n";