export default "import { Component, TemplateRef } from '@angular/core';\nimport { BsModalService, BsModalRef } from 'bao-tran-angular/modal';\n\n@Component({\n  selector: 'demo-modal-service-confirm-window',\n  templateUrl: './service-confirm-window.html'\n})\nexport class DemoModalServiceConfirmWindowComponent {\n  modalRef: BsModalRef;\n  message: string;\n  constructor(private modalService: BsModalService) {}\n\n  openModal(template: TemplateRef<any>) {\n    this.modalRef = this.modalService.show(template, {class: 'modal-sm'});\n  }\n\n  confirm(): void {\n    this.message = 'Confirmed!';\n    this.modalRef.hide();\n  }\n\n  decline(): void {\n    this.message = 'Declined!';\n    this.modalRef.hide();\n  }\n}\n";