export default "import { Component, TemplateRef } from '@angular/core';\nimport { BsModalService, BsModalRef } from 'bao-tran-angular/modal';\n\n@Component({\n  selector: 'demo-modal-service-disable-animation',\n  templateUrl: './disable-animation.html'\n})\nexport class DemoModalServiceDisableAnimationComponent {\n  modalRef: BsModalRef;\n  config = {\n    animated: true\n  };\n  constructor(private modalService: BsModalService) {}\n\n  openModal(template: TemplateRef<any>) {\n    this.modalRef = this.modalService.show(template, this.config);\n  }\n}\n";